

import React from 'react';

//componente
const TableHead = () => {
    return ( <thead>
<tr>
        <th> Nombre </th>
        <th> Oficio </th>
        <th> Borrar </th>
</tr>
    </thead>
       
    );
};

const TableBody = (props) => {
    return (<tbody>
    <tr>
        <td> Rossini </td>
        <td> Profesora </td>
    </tr>
 
    <tr>
        <td> Ignacio </td>si
        <td> Estudiante </td>
     </tr>
     </tbody>
     );
};



//Padre de la tabnla
function Table () {
    return (
        <div>
            <table>
        <TableHead/>
        <TableBody/>

            </table>
        </div>
    );
};

export default Table;