const Section =  () => {


    const [count,setCount] = useState(0) //valor inicial
    const [datos,setDatos] = useState('')
    const [act,setActualizacion] = useState('') //tareaaaaa


    function cambios (e){
        setActualizacion(e.target.value);
    }

    function mostrarTexto (e){
        document.getElementById("texto").textContent = act;
        e.preventDefault();
    }


function handleChange(e){

    setDatos (e.target.value);
}

function handleSubmit(e) {

    e.preventDefault()
    alert('El nombre ingresado es: ${datos}')
}

    function handleClick(){

        setCount(count +1);
    }

    //tareaaa 3 - creo objetp USER


    const user = { name:"Emanuel", apellido: "Vargas"};


    return (
        <section>
            <form onSubmit={handleSubmit}>
                <label>Nombre</label>
                <input type="text" value={datos} onChange={handleChange}/>
            <input type="submit"/>

        
            </form>



   
 <p> Haz hecho click {count} veces</p>
 <button onClick={handleClick}></button>
        <Table data={datos}/>




       
                <input type="text" value={act} onChange={cambios}/>
          
     <p id="texto"></p>

            <button onClick = {mostrarTexto} type="submit">Enviar</button>


<div> <NombreUsuario user = {user}/> </div>

 

        </section>
    )
}


export default Section;